.App {
  text-align: center;
  margin-top:30px
}

.App .input-data{
  height: 200px;
  margin-top: 30px;
}

select{
  margin:10px;
  height:40px;
  font-size: 20px;
  background-color: rgb(231, 231, 231);
  border:none;
}

input{
  margin:10px;
  height:37px;
  font-size: 20px;
  background-color: rgb(231, 231, 231);
  border:none;
}

#totalPrice{
  font-size: 30px;
}

#submit{
  margin-left:30px;
  font-size: 27px;
  border-radius: 10px;
  background-color: rgba(221, 221, 221, 0.63);
  border-color: rgb(142, 151, 156);
}

#warnning{
  color: rgb(255, 0, 2);
  margin-top:10px;
  font-weight: bold;
}

#item-list{
  width: 70%;
  margin: 10px;
  border-collapse: collapse;
}

th{
  background-color: rgb(57, 111, 182);
  color: #fff;
  padding-top: 10px;
  padding-bottom: 10px;
}

tbody tr:nth-child(2n+1){
  background-color: rgb(240, 240, 240);
}
tbody tr:nth-child(2n){
  background-color: rgb(214, 214, 214);
}